<template>
<div>
  <app-jumbotron :imageSrc="imageSrc"></app-jumbotron>
  <app-story></app-story>
</div>
</template>
<script>
import Jumbotron from './sections/Jumbotron.vue';
import Story from './sections/Story.vue';
var desktop = require("../assets/busFront.jpg");
var mobile = require("../assets/busFront.jpg");
export default {
  data() {
    return {
      imageSrc: {
        desktop: desktop,
        mobile: mobile,
        class: 'overlay bus',
        title: 'My Story'
      }
    }
  },
  components: {
    appJumbotron: Jumbotron,
    appStory: Story
  }
}
</script>



