ot to spend two weeks working with my teacher.\n      ")]),_vm._v(" "),_c('p',[_vm._v("\n        After my trip over the summer I decided to take the knives for a more permanent rout. I bought a full sized Blue Bird school bus from the Ashland School district in Oregon and converted it into my knife workshop and store front. The back half of the bus\n        is my work shop and the front half is a more inviting area for customers to look at finished knives on display. I decided to convert a mobile shop because it is an easy way for me to own my business without having to dive too deep into debt by\n        having to rent shop space as well as store front space. I can move this shop to whatever home I move into or whatever land I end up buying.\n      ")]),_vm._v(" "),_c('p',[_vm._v("\n        So keep an eye out! You may see a wandering knife making schoolbus driving around one day and I’ll be inside!!\n      ")])])])])}]
